<template>
    <div class="swiper-container">
        <div class="swiper-wrapper">
            <div class="swiper-slide" v-for="item in list">
                <img :src="item.img">
            </div>
        </div>
        <!-- 分页器 -->
        <div class="swiper-pagination swiper-pagination-bullets">
            <span class="swiper-pagination-bullet" v-for="item in list"></span>
        </div>       
    </div>
</template>

<script>
import swiper from '../../static/swiper.min.js'
import '../../static/swiper.css'
export default {
  data () {
    return {
      list:[
        {"i":0,"img":"static/banner_01.jpg"},
        {"i":1,"img":"static/banner_02.jpg"},
        {"i":2,"img":"static/banner_03.jpg"},
        {"i":3,"img":"static/banner_04.jpg"},
        {"i":4,"img":"static/banner_05.jpg"},
        {"i":5,"img":"static/banner_06.jpg"}
        ]
    }
  },
  mounted:function(){
    new Swiper ('.swiper-container', {
    direction: 'horizontal',//水平滑动
    loop: true,//循环播放
    autoplay:3000,//自动播放时间
    
    // 分页器
    pagination: '.swiper-pagination',//设置分页器
    paginationClickable:true, //点击分页器切换图片

    autoplayDisableOnInteraction:false,//当用户操作轮播后，自动轮播
    
  })        
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.swiper-container {
    width: 100%;
    overflow: hidden;
} 

.swiper-slide img{
    width: 100%;
    height: 100%;
} 
</style>



