<template>
  <div class="ui">
    <div style="display:none">{{can}}</div>
    <slot></slot>
    <div class="uiR" v-for="item in list" v-if="item.show">
        <div class="uiTop">
          <div class="uiLeft"><img :src="item.zhao"></div>
          <div class="uiRight">
            <ul class="uiMsg">
              <li>
                <span>{{item.name}}</span>
                <span>{{item.job}}</span>
                <span>{{item.qian}}/月</span>
              </li>
              <li>
                <span><img src="../assets/biaoqian.png">{{item.xue}}</span>
                <span><img src="../assets/wtime.png">{{item.zhuang}}</span>
              </li>
              <li>
                <img src="../assets/um.png" v-if="item.zheng==1" class="img1">
                <img src="../assets/man.png" v-if="item.sex==0" class="img2">
                <img src="../assets/woman.png" v-if="item.sex==1" class="img2"></li>
            </ul>
          </div>
        </div>
    <div class="uiBottom">
      <span v-for="ji in item.ji">{{ji}}</span>
    </div>
  </div>
  </div>
</template>

<script>
export default {
  props:['can'],
  name: 'ui',
  data () {
    return {
      list:[{zhao:'static/chen.png',name:'陈可',job:'UI设计师',qian:'5000',xue:'专科',zhuang:'在职--下月到岗',zheng:1,sex:1,show:true,ji:['平面设计','UI','网页设计','APP设计']},
      {zhao:'static/fan.png',name:'方泽飞',job:'UI设计师',qian:'8000',xue:'本科',zhuang:'在职--月内到岗',zheng:1,sex:0,show:true,ji:['平面设计','UI','网页设计']},
      {zhao:'static/liu.png',name:'刘依平',job:'UI设计师',qian:'10000',xue:'硕士',zhuang:'离职--随时到岗',zheng:0,sex:1,show:true,ji:['平面设计','UI','网页设计','交互设计']},
      {zhao:'static/chen.png',name:'陈可',job:'UI设计师',qian:'4000',xue:'专科',zhuang:'离职--随时到岗',zheng:0,sex:1,show:true,ji:['平面设计','UI','网页设计','APP设计']},
      {zhao:'static/fan.png',name:'方泽飞',job:'UI设计师',qian:'9000',xue:'本科',zhuang:'暂不考虑',zheng:1,sex:0,show:true,ji:['平面设计','UI','网页设计']},
      {zhao:'static/liu.png',name:'刘依平',job:'UI设计师',qian:'12000',xue:'博士',zhuang:'在职--下月到岗',zheng:1,sex:1,show:true,ji:['平面设计','UI','网页设计','交互设计']},
      {zhao:'static/chen.png',name:'陈可',job:'UI设计师',qian:'5000',xue:'专科',zhuang:'在职--下月到岗',zheng:1,sex:1,show:true,ji:['平面设计','UI','网页设计','APP设计']},
      {zhao:'static/fan.png',name:'方泽飞',job:'UI设计师',qian:'8000',xue:'本科',zhuang:'在职--月内到岗',zheng:1,sex:0,show:true,ji:['平面设计','UI','网页设计']},
      {zhao:'static/liu.png',name:'刘依平',job:'UI设计师',qian:'10000',xue:'硕士',zhuang:'离职--随时到岗',zheng:0,sex:1,show:true,ji:['平面设计','UI','网页设计','交互设计']},
      {zhao:'static/chen.png',name:'陈可',job:'UI设计师',qian:'4000',xue:'专科',zhuang:'离职--随时到岗',zheng:0,sex:1,show:true,ji:['平面设计','UI','网页设计','APP设计']},
      {zhao:'static/fan.png',name:'方泽飞',job:'UI设计师',qian:'9000',xue:'本科',zhuang:'暂不考虑',zheng:1,sex:0,show:true,ji:['平面设计','UI','网页设计']},
      {zhao:'static/liu.png',name:'刘依平',job:'UI设计师',qian:'12000',xue:'博士',zhuang:'在职--下月到岗',zheng:1,sex:1,show:true,ji:['平面设计','UI','网页设计','交互设计']},
      {zhao:'static/chen.png',name:'陈可',job:'UI设计师',qian:'5000',xue:'专科',zhuang:'在职--下月到岗',zheng:1,sex:1,show:true,ji:['平面设计','UI','网页设计','APP设计']},
      {zhao:'static/fan.png',name:'方泽飞',job:'UI设计师',qian:'8000',xue:'本科',zhuang:'在职--月内到岗',zheng:1,sex:0,show:true,ji:['平面设计','UI','网页设计']},
      {zhao:'static/liu.png',name:'刘依平',job:'UI设计师',qian:'10000',xue:'硕士',zhuang:'离职--随时到岗',zheng:0,sex:1,show:true,ji:['平面设计','UI','网页设计','交互设计']},
      {zhao:'static/chen.png',name:'陈可',job:'UI设计师',qian:'4000',xue:'专科',zhuang:'离职--随时到岗',zheng:0,sex:1,show:true,ji:['平面设计','UI','网页设计','APP设计']},
      {zhao:'static/fan.png',name:'方泽飞',job:'UI设计师',qian:'9000',xue:'本科',zhuang:'暂不考虑',zheng:1,sex:0,show:true,ji:['平面设计','UI','网页设计']},
      {zhao:'static/liu.png',name:'刘依平',job:'UI设计师',qian:'12000',xue:'博士',zhuang:'在职--下月到岗',zheng:1,sex:1,show:true,ji:['平面设计','UI','网页设计','交互设计']}]
    }
  },
  updated:function(){
    if(this.can[0]==0){
      for(var i=0;i<this.list.length;i++){
        if(this.can[1]=='全部'){
          this.list[i].show=true;
        }else{
          if(this.list[i].xue!=this.can[1]){
            this.list[i].show=false;
          }else{
             this.list[i].show=true;
          }
        }
      }
    }else if(this.can[0]==1){
      for(var i=0;i<this.list.length;i++){
        if(this.can[1]=='全部'){
          this.list[i].show=true;
        }else{
          if(this.list[i].zheng!=this.can[1]){
              this.list[i].show=false;
          }else{
              this.list[i].show=true;
          }
        }
      }
    }else if(this.can[0]==2){
      var arr=this.can[1].split('--');
      for(var i=0;i<this.list.length;i++){
        if(this.can[1]=='全部'){
          this.list[i].show=true;
        }else{
          if(this.list[i].qian>=arr[0]&&this.list[i].qian<=arr[1]){
            this.list[i].show=true;
          }else{
            this.list[i].show=false;
          }
        }
      }
    }else{
      for(var i=0;i<this.list.length;i++){
        if(this.can[1]=='全部'){
          this.list[i].show=true;
        }else{
          if(this.list[i].zhuang==this.can[1]){
            this.list[i].show=true;
          }else{
            this.list[i].show=false;
          }
        }
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.ui{
  width: 100vw;
  max-width: 750px;
  min-width: 320px;
  overflow: hidden;
  /*min-height: 50vh;*/
  background: #ededed;
  padding-bottom: 0.2rem;
  position: relative;
}

.uiR{
  width: 94%;
  max-width: 705px;
  min-width: 300.8px;
  height: 2.4rem;
  margin: 0.2rem auto 0;
  overflow: hidden;
  background: #fff;
}
.uiTop{
  width: 100%;
  height: 1.4rem;
  overflow: hidden;
  padding-top: 0.2rem;
}
.uiLeft{
  width: 1.25rem;
  height: 1.25rem;
  background: #ccc;
  border-radius: 50%;
  margin: 0 0.3rem;
  float: left;
  overflow: hidden;
}
.uiLeft img{
  width: 100%
}
.uiRight{
  width: 5rem;
  height: 1.25rem;
  float: left;
}
.uiMsg{
  width: 100%;
  overflow: hidden;
}
.uiMsg li{
  width: 5rem;
  height: 0.4rem;
  margin-top: 0.05rem;
}
.uiMsg li:nth-of-type(1) span{
  font-size: 0.3rem;
  color: #333;
}
.uiMsg li:nth-of-type(1) span:nth-of-type(1){
  margin-right: 0.14rem;
}
.uiMsg li:nth-of-type(1) span:nth-of-type(3){
  float: right;
  color: #ff7e00
}
.uiMsg li:nth-of-type(2) span{
  font-size: 0.26rem;
  color: #868686;
  margin-right: 0.4rem; 
}
.uiMsg li:nth-of-type(2) span img{
  width: 0.2rem;
  height: 0.2rem;
  margin-right: 0.1rem;
  margin-bottom: 0.07rem;
}
.uiMsg li:nth-of-type(3) img{
  margin-right: 0.1rem;
}
.uiMsg li:nth-of-type(3) .img1{
  width: 0.42rem;
  height: 0.24rem;
}
.uiMsg li:nth-of-type(3) .img2{
  width: 0.29rem;
  height: 0.24rem;
}
.uiBottom{
  height: 0.4rem;
  padding: 0.2rem 0;
  overflow: hidden;
}
.uiBottom>span{
  display: inline-block;
  padding: 0.1rem 0.3rem;
  background: #ddf4f3;
  height: 0.2rem;
  line-height: 0.2rem;
  font-size: 0.2rem;
  float: left;
  margin-left: 0.2rem;
  color: #a1a1a1;
}
</style>



