<template>
  <div id="app">
    <div id="topNav">
    <ul id="navs">
        <li v-for="(item,index) in navs" v-on:click="xuan(index)" :class="{zhong:appIndex==index}">{{item.value}}</li>
    </ul>
    <div id="sou"><input v-if="inputs" type="text"/></div>
    </div>
    <lun></lun>
    <ul id="nav2s">
        <li v-for="(item,index) in nav2s" @click="dian(index)">
          {{item.value}}
          <img src="./assets/xiala.png">
          <div v-if="item.chird,index==divIndex"><p v-for="chir in item.chird" @click="shai(index,chir)">{{chir==0?'无认证':chir==1?'有认证':chir=='全部'?'全部':chir}}</p></div>
        </li>
    </ul>   
    <router-view :can='can'><div :class="{zhe:myZhe}"></div></router-view>
  </div>
</template>

<script>
import lun from './components/lun.vue'

export default {
  name: 'app',
  data:function(){
      return {
          navs:[{value:'UI',path:'/'},{value:'H5',path:'/H5'},{value:'Web前端',path:'/Web'},{value:'Php开发',path:'/Php'},{value:'Java开发',path:'/Java'}],
          nav2s:[{value:'学历',chird:['全部','专科','本科','硕士','博士']},
                 {value:'有码认证',chird:['全部',0,1]},
                 {value:'薪资',chird:['全部','2000--4000','4000--6000','6000--8000','8000--10000','10000--12000']},
                 {value:'状态',chird:['全部','离职--随时到岗','在职--月内到岗','在职--下月到岗','暂不考虑']}],
          inputs:false,
          appIndex:0,
          divIndex:-1,
          path:null,
          ulIndex:null,
          can:[],
          x:0,
          myZhe:false
      }
  },
  components:{lun},
  methods:{
    xuan:function(index){
      this.appIndex=index;
      this.path=this.navs[index].path;
      this.$router.push(this.path);
      if(this.can.length==0){
          this.can[0]=index;
          this.can[1]='全部';
      }
    },
    dian:function(index){
      if(this.x==0){
          this.divIndex=index;
          this.myZhe=true;
          if(this.can.length==0){
            this.can[0]=index;
            this.can[1]='全部';
          }
      }else{
        this.divIndex=-1;
        this.x=0
      }    
    },
    shai:function(index,chir){
      this.x=1;
      this.can=[];
      this.can.push(index);
      this.can.push(chir);
      this.myZhe=false;
    }
  }
}
</script>
<style>

body{
  width: 100%;
  min-width: 320px;
  max-width: 750px;
  background: #fff;
  margin: 0 auto;
}

a{
  text-decoration: none;
  color: #7f7f7f;
}

.zhong{
  font-weight: bold;
  color: #333;
}

.zhe{
  position: absolute;
  width: 100%;
  max-width: 750px;
  height: 100%;
  background: #000;
  filter:alpha(opacity=30);
  opacity: 0.3;
}

#app {
  width: 100%;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  background: #fff;
}
#app ul li{
  list-style-type:none;
  float:left;
}

#app #topNav{
  width: 100%;
  max-width: 750px;
  overflow: hidden;
}
#app #navs{
  width:87%;
  height: 0.88rem;
  float:left;
}

#app #navs>li{
  /*width: 20%;*/
  height: 0.88rem;
  line-height: 0.88rem;
  font-size: 0.3rem;
  text-align:center;
  padding: 0 0.2rem
}

#app #sou{
  width:13%;
  height:0.88rem;
  float:left;
  background: url(./assets/fang.png) left center no-repeat;
  background-size: 0.32rem 0.32rem; 
}
#app #sou input{
  float:left
}

#app #lun{
  width: 100%;
  height: 3.6rem;
  overflow: hidden;
}


#app #nav2s{
  width:100%;
  height:0.88rem;
  position: relative;
}
#app #nav2s>li{
  width:25%;
  height: 0.88rem;
  font-size:0.26rem;
  text-align:center;
  line-height:0.88rem;
  color: #818181;
}
#app #nav2s>li>img{
  width: 0.20rem;
  height: 0.11rem;
  text-align: top;
  margin-left: 0.1rem;
  margin-bottom: 0.03rem;
}
#app #nav2s>li>div{
  position: absolute;
  background: #fff;
  width: 100%;
  left: 0;
  z-index: 10;
}
#app #nav2s>li>div>p{
  text-align: left;
  padding-left: 0.5rem;
}
#app #nav2s>li>div>p:nth-of-type(n+1){
  border-top: 1px solid #ccc;
}
</style>



